import { Button } from '@material-ui/core'
import React from 'react'
import "../Style/Login.css"
import { auth, provider } from "./firebase"
import companyLogo from "../Data/Escudo colegio.png"

function Login() {
    const signIn = () => {
        auth.signInWithPopup(provider).catch((error) => alert(error.message));
    }
    return (
        <div className='login'>
            <header>
                <img alt="LogoImage" id="LogoImage" src={companyLogo}/>
                <div id="headerDiv">              
                    <nav class="NavBar">
                        <ul class="NavMenu">
                            <li><a href="https://www.mensajeriaijr.ml/">Inicio</a></li>
                            <li><a href="https://www.mensajeriaijr.ml/Contenido/Acerca">Acerca</a></li>
                            <li id="NavButton"><a href="https://www.mensajeriaijr.ml/Home">Iniciar Sesión</a></li>
                        </ul>
                    </nav>
                </div>
            </header>
            <div class="Title">Inicio de sesión</div>
            <div className="ButtonContainer">
                <Button className="btnLog" onClick={signIn}> Acceder </Button>
            </div>
            <footer>
                <div class="FooterContainer">
                    <div class="Footer">
                        <div class="FooterHeading Footer1">
                            <h2>Acerca de nosotros</h2>
                            <a href="https://www.mensajeriaijr.ml/Contenido/Acerca">Acerca</a>                       
                            <a href="https://www.mensajeriaijr.ml/Contenido/T%C3%A9rminos%20y%20condiciones">Términos de servicio</a>
                        </div>
                        <div class="FooterHeading Footer2">
                            <h2>Contáctanos</h2>
                            <a href="https://www.mensajeriaijr.ml/Contenido/Soporte">Soporte</a>
                            <a href="https://www.mensajeriaijr.ml/Contenido/Contacto">Contacto</a>
                        </div>
                        <div>

                        </div>
                    </div>
                </div>
        </footer>
        </div>
    )
}

export default Login;